import { ref, watch } from 'vue';
export default {
  name: 'BottomView',
  props: ['isUserAgreement', 'modifyUserAgreement', 'userAgreementEvent', 'privacyPolicyEvent'],

  setup(props, context) {
    const userAgreement = ref(props.isUserAgreement);
    watch(userAgreement, (newValue, oldValue) => {
      props.modifyUserAgreement(newValue);
    });
    return {
      userAgreement
    };
  }

};