import Api from '@/Api/Api';
import { useRoute, useRouter } from 'vue-router';
import JSEncrypt from 'jsencrypt';
import { Toast } from 'vant';
import cache from '@/common/cache';
import TextField from '@/main-module/login/view/TextField';
import ForgotPasswordTextField from '@/main-module/login/view/ForgotPasswordTextField';
import VerifyCodeTextField from '@/main-module/login/view/VerifyCodeTextField';
import SendCodeTextField from '@/main-module/login/view/SendCodeTextField';
import viewModel from '@/main-module/login/viewModel/viewModel';
import HeadView from '@/main-module/login/view/HeadView';
import BottomView from '@/main-module/login/view/BottomView';
import { ref } from 'vue';
export default {
  name: 'LoginController',
  components: {
    HeadView,
    BottomView,
    TextField,
    ForgotPasswordTextField,
    VerifyCodeTextField,
    SendCodeTextField
  },

  setup() {
    const {
      formData,
      event
    } = viewModel;
    const data = formData;
    const router = useRouter();
    const show = ref(false);

    const onCancel = () => Toast('');

    const actions = [{
      name: '',
      value: 'user'
    }, {
      name: '',
      value: 'management'
    }];

    const onSelect = item => {
      show.value = false;
      cache.saveToken('test');
      cache.setValue('appType', item.value);
      router.push({
        name: 'tabBar'
      });
    };

    const {
      userAgreementEvent,
      privacyPolicyEvent,
      changeEvent,
      loadVerifyCodeImage
    } = event(data, router);
    const route = useRoute();

    const modifyUserAgreement = () => {
      data.isUserAgreement = !data.isUserAgreement;
    };

    const loginEvent = () => {
      route.meta.requireAuth = true;

      if (!data.isUserAgreement) {
        return;
      }

      let api = null;

      if (data.loginType) {
        if (data.phone.length === 0) {
          Toast.fail('');
          return;
        }

        if (data.phone.length !== 11) {
          Toast.fail('');
          return;
        }

        if (data.verificationCode.length === 0) {
          Toast.fail('');
          return;
        }

        api = Api.merchantUser.login({
          username: data.phone,
          password: '',
          verificationCode: data.verificationCode
        });
      } else {
        if (data.username.length === 0) {
          Toast.fail('');
          return;
        }

        if (data.password.length === 0) {
          Toast.fail('');
          return;
        }

        if (data.verificationCode.length === 0) {
          Toast.fail('');
          return;
        }

        const rsa = new JSEncrypt();
        rsa.setPublicKey('MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCWqmlnUmee2iILSz0HR7Z3dfvk1YqLR+sOb7+ZKsd4fXmh+ruwSFQjGLmySv2ZEs8gLNDqml84+AkH9ZLlAEoMCWT/MPiOwoQxr9uVDYcQUkxh1AsC1fEVD89EZpsZHGmEhw3/sRCeA3ZDhxVElpqi1RzWAKMT066BH/F0/9qZrQIDAQAB');
        const rasPassword = rsa.encrypt(data.password);
        api = Api.merchantUser.login({
          username: data.username,
          password: rasPassword,
          verificationCode: data.verificationCode
        });
      }

      show.value = true;
      return; // eslint-disable-next-line no-unreachable

      api.then(response => {
        const token = response.token || '';

        if (token.length > 0) {
          cache.setValue('token', token);
          cache.setValue('loginInfo', response);
          router.push({
            name: 'workbench'
          });
        } else {
          Toast.fail('');
        }
      }).catch(error => {
        Toast.fail(error.message);
        loadVerifyCodeImage();
      });
    };

    return {
      data,
      userAgreementEvent,
      privacyPolicyEvent,
      changeEvent,
      loginEvent,
      loadVerifyCodeImage,
      modifyUserAgreement,
      show,
      actions,
      onCancel,
      onSelect
    };
  }

};