import TextField from '@/main-module/login/view/TextField';
import { computed, reactive, ref, toRefs } from 'vue';
import { Toast } from 'vant';
export default {
  name: 'SendCodeTextField',
  props: ['title', 'type', 'placeholder', 'valueKey', 'top', 'eyes', 'defaultValue', 'changeValueEvent', 'phone'],
  components: {
    TextField
  },

  setup(props, context) {
    const countdown = ref(-1);
    const countdownTitle = computed(() => {
      return countdown.value === -1 ? '' : `${countdown.value}s`;
    });
    const state = reactive(props);
    const {
      phone
    } = toRefs(state);

    const sendVerifyCodeEvent = event => {
      if (countdown.value !== -1) {
        return;
      }

      if (phone.value.length === 0) {
        Toast.fail('');
        return;
      }

      const reg = /^1[3-8]+\d{9}$/;

      if (!reg.test(phone.value)) {
        Toast.fail('');
        return;
      }

      countdown.value = 60; // inputNode.value.focus()

      let timer = null;
      timer = setInterval(() => {
        countdown.value -= 1;
        console.log(countdown.value);

        if (countdown.value === -1) {
          console.log('clearInterval');
          clearInterval(timer);
        }
      }, 1000);
    };

    return {
      countdown,
      countdownTitle,
      sendVerifyCodeEvent
    };
  }

};