import { reactive, onMounted } from 'vue'
import Api from '@/Api/Api'

const formData = reactive({
  loginType: false,
  username: 'greeadmin',
  phone: '',
  password: 'abcd4321',
  verificationCode: '',
  isUserAgreement: true,
  verifyCode: {
    captchaKey: '',
    expire: 0,
    base64Img: ''
  }
})

function event (data, router) {
  const userAgreementEvent = (event) => {
    console.log('userAgreementEvent', router)
    event.stopPropagation()
    router.push({
      name: 'webView',
      query: {
        url: 'https://greesale.gree.com/appDownload/userService.html',
        title: ''
      }
    })
  }
  const privacyPolicyEvent = (event) => {
    console.log('userAgreementEvent', router)
    event.stopPropagation()
    router.push({
      name: 'webView',
      query: {
        url: 'https://greesale.gree.com/appDownload/private.html',
        title: ''
      }
    })
  }
  const changeEvent = (value, type) => {
    console.log(value, type)
    data[type] = value
  }
  const loadVerifyCodeImage = () => {
    Api.merchantUser.verifyCodeImage().then((response) => {
      Object.assign(data.verifyCode, response)
    }).catch((error) => {
      console.log(error)
    })
  }
  onMounted(loadVerifyCodeImage)
  return {
    userAgreementEvent,
    privacyPolicyEvent,
    changeEvent,
    loadVerifyCodeImage
  }
}

export default {
  formData,
  event
}
