import { ref, computed } from 'vue';
export default {
  name: 'TextField',
  props: ['title', 'type', 'placeholder', 'valueKey', 'top', 'eyes', 'changeValueEvent', 'defaultValue'],

  setup(props, context) {
    const selected = ref('0');

    const blurEvent = () => {
      selected.value = '0';
    };

    const changeEvent = event => {
      props.changeValueEvent(event.target.value, props.valueKey); // context.emit('changeEvent', event.target.value, props.valueKey)
    };

    const isGoggle = ref(false);
    const image = computed(() => isGoggle.value ? require('@/assets/img/icons/@2x.png') : require('@/assets/img/icons/@2x.png'));
    const inputType = computed(() => {
      if (props.eyes) {
        return isGoggle.value ? 'text' : 'password';
      }

      return props.type;
    });
    return {
      selected,
      blurEvent,
      changeEvent,
      isGoggle,
      image,
      inputType
    };
  }

};