import Axios from '@/Api/Axios'

class MerchantUser {
  verifyCodeImage () {
    return Axios({
      url: '/gsale-merchant/merchantUser/getImage',
      method: 'get'
    })
  }

  login (data) {
    return Axios({
      url: '/gsale-authentication/login',
      method: 'post',
      data: data
    })
  }
}

export default new MerchantUser()
